/* Solar Calculator Styling */
.solar-calculator-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #333;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
}

/* Step Indicators */
.step-indicators {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  position: relative;
}

.step-indicators::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #e0e0e0;
  z-index: 1;
}

.step-indicator {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  border: 2px solid #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  position: relative;
  z-index: 2;
}

.step-indicator.active {
  border-color: #4CAF50;
  color: #4CAF50;
  box-shadow: 0 0 0 4px rgba(76, 175, 80, 0.2);
}

.step-indicator.completed {
  background-color: #4CAF50;
  border-color: #4CAF50;
  color: white;
}

/* Form Steps */
.form-steps {
  margin-bottom: 2rem;
}

.form-step {
  display: none;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.step-title {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.step-description {
  margin-bottom: 2rem;
  color: #666;
  font-size: 1rem;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #444;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  color: #333;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.2);
}

.form-group input.error,
.form-group select.error {
  border-color: #f44336;
}

/* Navigation Buttons */
.nav-container {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

.button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}

.primary-button {
  background-color: #4CAF50;
  color: white;
}

.primary-button:hover {
  background-color: #43a047;
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);
}

.secondary-button {
  background-color: #f5f5f5;
  color: #333;
}

.secondary-button:hover {
  background-color: #eeeeee;
}

/* Results Container */
.result-container {
  display: none;
  animation: fadeIn 0.8s ease;
}

.result-header {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #2E7D32;
}

.personal-message {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 1.1rem;
  color: #555;
}

/* Results Grid */
.results-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2.5rem;
}

.result-item {
  background-color: #f9f9f9;
  padding: 1.5rem;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s;
}

.result-item:hover {
  transform: translateY(-5px);
}

.result-icon {
  width: 50px;
  height: 50px;
  margin: 0 auto 1rem;
  fill: #4CAF50;
}

.result-icon svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.result-title {
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #555;
}

.result-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2E7D32;
}

/* Call to Action */
.cta-container {
  text-align: center;
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #f1f8e9;
  border-radius: 10px;
}

.cta-text {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
}

.cta-button {
  padding: 14px 28px;
  font-size: 1.1rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .solar-calculator-container {
    padding: 1.5rem;
    border-radius: 8px;
  }
  
  .step-indicator {
    width: 32px;
    height: 32px;
    font-size: 0.9rem;
  }
  
  .results-grid {
    grid-template-columns: 1fr;
  }
}

/* Hidden Webflow Form */
.hidden-solar-form {
  display: none;
} 